<template>
  <div class='confirmOrder-box'>
    <div class="doctor-title">
      <img src="../../../assets/img/yisheng.png" class="ml-15"/>
      <span class="doctorname ml-10">唐宇</span>
      <span class="doctorposition ml-10">主任医生</span>
    </div>
    <div class="orderinformation">
      <p><span class="sp1">就诊科室</span><span class="sp2">康复医学科</span></p>
      <p><span class="sp1">就诊时间</span><span class="sp2">2019年11月25日 星期一 下午</span></p>
      <p><span class="sp1">费　　用</span><span class="sp3">￥ 0</span></p>
      <p><span class="sp1">费用明细</span><span class="sp3">挂号费: ￥ 0</span></p>
    </div>
    <div class="visitingPerson">
      <van-field
        readonly
        clickable
        label="就诊人"
        :value="value"
        placeholder="请点击选择就诊人"
        @click="showPicker = true"
      />
      <van-popup v-model="showPicker" position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
      <van-field
        readonly
        clickable
        label="预约号源"
        :value="value"
        placeholder="请点击选择号源"
        @click="showPicker = true"
      />
      <van-popup v-model="showPicker" position="bottom">
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="showPicker = false"
          @confirm="onConfirm"
        />
      </van-popup>
    </div>
    <van-button type="primary" size="large" class="mt-20" @click="goPaymentFn">确认挂号</van-button>
  </div>
</template>

<script>
  export default {
    name: '',
    data(){
      return{
      value: '',
      showPicker: false,
      columns: ['杭州', '宁波', '温州', '嘉兴', '湖州']
      }
    },
    methods: {
      onConfirm(value) {
        this.value = value;
        this.showPicker = false;
      },
      goPaymentFn(){
        this.$router.push({path:'/payment',query:{setid:123456}})
      }
    }
  };
</script>
<style lang='scss'>
.confirmOrder-box{
  .visitingPerson{
    margin-top: 10px;
  }
  .orderinformation{
    background: #fff;
    padding: 10px 0;
    p{
      font-size: 14px;
      padding-left: 10px;
      margin-bottom: 5px;
      .sp1{
        color: #999;
        margin-right: 20px;
      }
      .sp3{
        color: #FFB30B
      }
    }
  }
  .doctor-title{
    background: #fff;
    margin-top: 10px;
    padding: 10px 0;
    position: relative;
    img{
      width: 2rem;
      height: 2rem;  
    }
    .doctorposition{font-size: 12px;color: #666;}
  }
  .doctor-title::after {
    position: absolute;
    box-sizing: border-box;
    content: ' ';
    pointer-events: none;
    right: 0;
    bottom: 0;
    left: 0px;
    border-bottom: 1px solid #ddd;
    -webkit-transform: scaleY(.5);
    transform: scaleY(.5);
}
.doctor-title::before {
    position: absolute;
    box-sizing: border-box;
    content: ' ';
    pointer-events: none;
    right: 0;
    top: 0;
    left: 0px;
    border-bottom: 1px solid #ddd;
    -webkit-transform: scaleY(-.5);
    transform: scaleY(-.5);
}
}
</style>


